header{
    height: auto;
    width: 100%;
    background-color:black;
    height: 80px;

    position: fixed;

    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width: 990px) {
    header {
      height:54px;
    }
  }

header .global-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
svg{
    width: 130px;
    fill: white;
}

@media only screen and (max-width: 990px) {
    svg {
      width: 88.5px;
    }
  }

.desktop-nav{
    display: flex;
}

@media only screen and (max-width: 990px) {
    .desktop-nav{
      display: none;
    }
  }


.desktop-nav a, h1{
    color:white;
    padding: 28px;
    font-weight: bold;
    font-size: 1.25em;
    letter-spacing: 0.5px;
    display: inline;
    cursor: pointer;
    font-size: 16px;
}


.profile-container{
    display: flex;
    flex-direction: column;
}

.drop-menu{
    display: flex;
    flex-direction: column;
    background-color:white;
    position: absolute;
    top:70px;
    border-radius: 3px;
}

.drop-menu a{
    color:black;
    padding:13px 28px;
    font-size: 1.2em;
}

.mobile-menu{
    color: white;
    font-size: 20px;
}

@media only screen and (min-width: 990px) {
    .mobile-menu{
      display: none;
    }
  }

